import React, { useEffect, useState } from "react";
import { parseLink } from "./helpers";

const Period = (props) => {
  // Date.now()
  const [display, setDisplay] = useState("loading");
  const [link, setLink] = useState(null);

  // eslint-disable-next-line react-hooks/exhaustive-deps
  useEffect(() => {
    // This will run every frame update
    // only after the use effect bellow this one finishes, nop
    const { timeSlot, isWeekend } = props;

    if (isWeekend) {
      setDisplay("Imagine Having School on the Weekends");
      return;
    }
    let richContext = "";
    if (timeSlot !== undefined) {
      // logic
      let loadedLink = null; // link shenanigans
      let prefix = ""; // display prefix
      if (timeSlot.richContext !== undefined) {
        // regular context
        const loadedRC = localStorage.getItem("VRA_" + timeSlot.richContext);
        if (loadedRC !== null) richContext = ` | ${loadedRC}`;

        // during session link
        loadedLink = parseLink(
          localStorage.getItem("VRA_LINK_" + timeSlot.richContext)
        );
      }

      if (timeSlot.mRichContext) {
        // Transition Context
        const loadedFrom = localStorage.getItem(
          "VRA_" + timeSlot.mRichContext.from
        );
        const loadedTo = localStorage.getItem(
          "VRA_" + timeSlot.mRichContext.to
        );
        if (loadedFrom !== null && loadedTo !== null) {
          richContext = ` | ${loadedFrom} â†’ ${loadedTo}`;
        }

        // transition link
        loadedLink = parseLink(
          localStorage.getItem("VRA_LINK_" + timeSlot.mRichContext.to)
        );
      }
      // set link
      if (loadedLink !== null) {
        setLink(loadedLink);
        prefix = "ðŸ”— "; // custom prefixing
      }

      // set rich context
      setDisplay(`${prefix}${timeSlot.event}${richContext}`);
    }
  });

  return (
    <p
      onClick={() => {
        link !== null ? window.open(link) : console.log("nolink");
      }}
    >
      {display}
    </p>
  );
};

export default Period;
