import React, { useState } from "react";

function ColorSelect() {
  const [color, setColor] = useState(localStorage.getItem("VRA_COLOR"));

  const handleChange = (e) => {
    const newColor = e.target.value;
    localStorage.setItem("VRA_COLOR", newColor);
    setColor(newColor);
  };

  const handleReset = () => {
    localStorage.setItem("VRA_COLOR", "#282c34");
    setColor("#282c34");
  };

  return (
    <>
      <h2>Background</h2>
      <div className="colorSelect">
        <input
          type="color"
          id="head"
          name="head"
          value={color === null ? "#282c34" : color}
          className="selector"
          onChange={handleChange}
        />
        <button onClick={handleReset}>Reset Color</button>
      </div>
    </>
  );
}

export default ColorSelect;
